.modal{
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0);
}

.modal-container{
    padding: 20px;
    background-color: whitesmoke;
    border-radius: 10px;
}

.modal.active{
 transform: scale(1);
}